/*fonts*/
	@font-face {
		font-family: 'MyWebFont';
		src: url('webfont.eot'), /* IE9 Compat Modes */
			 url('webfont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
			 url('webfont.woff2') format('woff2'), /* Super Modern Browsers */
			 url('webfont.woff') format('woff'), /* Pretty Modern Browsers */
			 url('webfont.ttf')  format('truetype'), /* Safari, Android, iOS */
	  		 url('webfont.svg#svgFontName') format('svg'), /* Legacy iOS */
	  		 url("webfont.otf") format("opentype");
	}
/*fonts*/
/*global*/
	* {
		margin: 0;
		padding: 0;
		box-sizing: border-box;
		outline: none;

	}
	body {
		font-family: 'Montserrat', sans-serif;
		background: #fff;
	}
	button {
		cursor: pointer;
	}
	button, input, textarea {
		color: inherit;
		transition: 0.5s;
	}
	h1,h2,h3,h4,h5,h6,p {
		font-size: inherit;
	}
	a, a:visited, a:focus {
		color: inherit;
		text-decoration: none;
		transition: 0.5s;

	}
	address {
		font-style: normal;
	}
	.center {
		padding-left: calc(50vw - 595px);
		padding-right: calc(50vw - 595px);
	}
	.d-flex {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
	}
	.visually_hidden {
		position: absolute;
		width: 1px;
		height: 1px;
		overflow: hidden;
		clip: rect(0 0 0 0);
	}
	.btn-swipe {
		position: relative;
		display: block;
		overflow: hidden;
		width: 100%;
		height: 80px;
		max-width: 250px;
		margin: 1rem auto;
		text-transform: uppercase;
		border: 1px solid #979f5f;
		background: none;
		z-index: 5;
		&:before {
			content:'';
			display: block;
			position: absolute;
			left: 0;
			top:0;
			width: 0;
			height: 100%;
			transition:0.5s;
			background:#979f5f;
			z-index: -1;
		}
		&:hover {
			&:before {
				width: 100%;
			}
		}
	}

	
/*global*/

@media screen and (max-width: 520px) {
	.btn-swipe {
		max-width: 150px;
		height: 40px;
	}
}